import {Component, OnInit} from '@angular/core';
import {DashboardService} from '../../services/dashboard.service';
import {AppState} from '../../../../shared/sessions/app.state';
import {Store} from '@ngrx/store';
import * as UserDetailActions from './../../../../shared/sessions/actions/user.actions';
import {UserDetailModel} from '../../../../models/user-detail.model';
import {AuthService} from '../../../auth/components/services/auth.service';
import {Router} from "@angular/router";
import {NotificationService} from "../../../../shared/services/notification.service";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  isShowing = false;
  userDetails: UserDetailModel;

  constructor(private dashboardService: DashboardService, private store: Store<AppState>,
              private authService: AuthService, private router: Router, private notificationService: NotificationService) {
    this.dashboardService.getUserData().subscribe((res) => {
      this.store.dispatch(new UserDetailActions.UserDetailAction(res));
    });
  }

  showNavbar(): void {
    if (this.isShowing) {
      this.isShowing = false;
    } else {
      this.isShowing = true;
    }
  }

  signOut(): void{
    this.authService.signOutJWT({refresh: this.authService.getRefreshToken()}).subscribe((res) => {
      this.router.navigate(['signin']);
      localStorage.clear();
      this.notificationService.showNotification('You have been logged out successfully!', 'info');
    });
  }

  ngOnInit(): void {
    this.store.select('userDetails').subscribe((output) => {
      this.userDetails = output;
    });
  }

}
